const emptyFilter = (item) => {

    return Array.isArray(item.sessions) && item.sessions.length > 0;

};

const sessionFilter = (session) => {

    return session.vaccine != "COVISHIELD";
};

const myFunction = (item, index, arr) => {

    item.sessions = item.sessions.filter(sessionFilter);
    arr[index] = item;
}

vaccineAvailabilityCenters2.forEach(myFunction);

