import React from "react";
import "./App.css";
import { vaccineAvailabilityCenters } from "./sampleData.js";
import  VaccineSessions  from "./VaccineSessions.js";

const HomePageHeader = () => {
    return (
      <header className="header">
        <h2>Your Stock Tracker</h2>
      </header>
    );
  };
  
  const VaccineAvailabilityCenter = ({ centerName, centerAddress, centerBlock, centerPinCode, centerFeeType, sessions }) => {
    if (!centerName) return <div />;
    return (
      <table>
        <tbody>
          <tr>
            <td>
              <h5>{centerName}</h5>
              <p>{centerAddress}</p>
              <p>{centerBlock}, {centerPinCode}</p>
              <p>{centerFeeType}</p>
            </td>
            <td>

<ol>
                {sessions.map((data, key) => {
          return (
                <li>

                    <p><b>Date: </b>{data.date}</p>
                    <p><b>Available: </b>{data.available_capacity}</p>
                    <p><b>Min Age Limit: </b>{data.min_age_limit}</p>
                    <p><b>Vaccine: </b>{data.vaccine}</p>
                    <p><b>Slots: </b>{data.slots}</p>
                
                </li>
          );
        })}
</ol>

            </td>
          </tr>
        </tbody>
      </table>
    );
  };



export const VaccineAvailabilityData = () => {
  return (
    <>
       <HomePageHeader />
       <div>

      {

      
      vaccineAvailabilityCenters.map((data, key) => {
          return (
            <div key={key}>
            <VaccineAvailabilityCenter 
            key={key}
            centerName = {data.name}
            centerAddress = {data.address}
            centerBlock = {data.block_name}
            centerPinCode = {data.pincode}
            centerFeeType = {data.fee_type}
            sessions = {data.sessions}
            />
            </div>
          );
        })}

      </div>

      { <VaccineSessions /> }
    </>
  );
};